/* tslint:disable:no-null-keyword */
/* tslint:disable:no-bitwise */

/**
 * @hidden
 */
export const isPresent: Function = (value: any): boolean => value !== null && value !== undefined;

/**
 * @hidden
 */
export const isChanged: Function = (propertyName: string, changes: any): boolean => (
    changes[propertyName] && !changes[propertyName].isFirstChange() &&
        changes[propertyName].previousValue !== changes[propertyName].currentValue
);

/**
 * @hidden
 */
export const guid: Function = (): string => {
    let id: string = "";
    let i: number;
    let random: number;

    for (i = 0; i < 32; i++) {
        random = Math.random() * 16 | 0;

        if (i === 8 || i === 12 || i === 16 || i === 20) {
            id += "-";
        }
        id += (i === 12 ? 4 : (i === 16 ? (random & 3 | 8) : random)).toString(16);
    }

    return id;
};

/**
 * @hidden
 */
export const isDocumentAvailable: Function = (): boolean => {
        return typeof document !== 'undefined';
};




